@charset "utf-8";
/* CSS Document */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  src: local('Roboto Light'), local('Roboto-Light'), url(https://fonts.gstatic.com/s/roboto/v18/0eC6fl06luXEYWpBSJvXCBJtnKITppOI_IvcXXDNrsc.woff2) format('woff2');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 300;
  src: local('Montserrat Light'), local('Montserrat-Light'), url(https://fonts.gstatic.com/s/montserrat/v12/IVeH6A3MiFyaSEiudUMXExNcqx07xvyppV96iFRdwiM.woff2) format('woff2');
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
html, body {
    height: 100%;
	width:100%;
    font-size: 16px;
    color: #f5f5f5;
    font-family: "Roboto", sans-serif;
    vertical-align: baseline;
	padding:0;
	margin:0;
}
::-webkit-scrollbar {width: 5px;}
::-webkit-scrollbar-track {
    background-color: #ebebeb;
    -webkit-border-radius: 10px;
    border-radius: 10px;
}
::-webkit-scrollbar-thumb {
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: #0f3567; 
}
ul,label{
	margin:0;
	padding:0;
}
body a {
	text-decoration:none;
	transition:0.5s all;
	-webkit-transition:0.5s all;
	-moz-transition:0.5s all;
	-o-transition:0.5s all;
	-ms-transition:0.5s all;
}
a:active,a:hover,a:focus {outline: none; text-decoration: none;}
.container-fluid{
	width:100%;
	padding-left:0px;
	padding-right:0px;
	margin-left:0px;
	margin-right:0px;
}
button:focus {outline: none;}
:before,
:after {
    transition: all .3s;
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
}

/***********============================================== header_topbar =====================================================***********/
.navigation{padding: 0 15px; z-index: 99; position: relative;}
@media only screen and (max-width: 600px) {.header_topbar{padding:5px 20px;}}
@media only screen and (min-width: 600px) {.header_topbar{padding:5px 20px;}}
@media only screen and (min-width: 768px) {.header_topbar{padding:5px 20px;}} 
@media only screen and (min-width: 992px) {.header_topbar{padding:5px 50px 0px 50px;}} 
@media only screen and (min-width: 1200px) {.header_topbar{padding:5px 100px 0px 100px;}}
@media only screen and (min-width: 1500px) {.header_topbar{padding:5px 160px 0px 160px;}}
.header_topbar .mt10{margin-top:13px;}
.header_topbar .mt15{margin-top:15px;}
.header_topbar .mt30{margin-top:30px;}
.header_topbar{
	position: relative;
	color: #fff;
    font-weight: 400;
	display:block;
	z-index:1;
    font-size: 15px;
    background: #0f3567 !important;
}
.header_topbar p{
    font-family: 'DM Sans', sans-serif;
	font-weight: 400;
    font-size: 16px;
    line-height: 25px;
    color: #fff;
    padding-top: 8px;
    margin-bottom: 0ox;
}
.header_topbar .links {margin-top:7px;}
.header_topbar .links li{
	font-family: 'DM Sans', sans-serif;
	font-weight: 400;
	position: relative;
    margin-right: 10px;
    font-size: 16px;
    line-height: 25px;
    color: #fff;
    display: inline-block;
    background: #008bf9;
    border-radius: 9px;
    padding: 1px 15px;
}
.header_topbar .links li a{color:#fff; font-weight:400;}
.header_topbar .links li .fa{color: #fff; font-size:14px; font-weight:400; padding-right:5px;}
.header_topbar .social:last-child{margin-bottom: 0;}
.header_topbar .social li{
	display: inline-block;
    border-bottom: none;
    border-right: none;
    border-top: none;
	line-height:40px;
    width: 40px;
    height: 40px;
    text-align: center;
}
.header_topbar .social li a{background:#36619a; color:#fff; border-radius: 50%;}
.header_topbar .social li a i{
    font-size: 14px;
	padding-top:0px;
    line-height: 24px;
} 
/***********============================================== header_topbar =====================================================***********/

