@media only screen and (min-width: 600px) {.header_bottom{padding: 20px 10px 0px 10px;} .logo{text-align:center;} .logo img{margin:0 auto; text-align:center; width: 70%; height: 70%;}}
@media only screen and (min-width: 768px) {.header_bottom{padding: 5px 20px 5px 20px;} .logo{text-align:left;} .logo img{margin:0px 0px; text-align:left; width: 80%; height: 80%;}} 
