/***********============================================== slider =====================================================***********/
@media only screen and (max-width: 600px) {.slider{margin-top:0px;} .slider .carousel-caption h2{font-size: 20px; line-height:30px; width: 100%;} .slider img{width:100% !important; height:100% !important;} .slider .carousel-caption .back{padding: 20px 20px; display: none;}}
@media only screen and (min-width: 600px) {.slider{margin-top:0px;} .slider .carousel-caption h2{font-size: 20px; line-height:30px; width: 100%;} .slider img{width:100% !important; height:100% !important;} .slider .carousel-caption .back{padding: 20px 20px; display: none;}}
@media only screen and (min-width: 768px) {.slider{margin-top:-20px;} .slider .carousel-caption h2{font-size: 43px; line-height:50px; width: 100%;} .slider img{width:100% !important; height:100% !important;} .slider .carousel-caption .back{padding: 80px 40px; display: block;}} 
@media only screen and (min-width: 992px) {.slider{margin-top:-20px;} .slider .carousel-caption h2{font-size: 60px; line-height:75px; width: 100%;} .slider img{width:100% !important; height:auto !important;} .slider .carousel-caption .back{padding: 120px 40px; display: block;}} 
@media only screen and (min-width: 1200px) {.slider{margin-top:-20px;} .slider .carousel-caption h2{font-size: 63px; line-height:75px; width: 100%;} .slider img{width:100% !important; height:auto !important;} .slider .carousel-caption .back{padding: 120px 40px; display: block;}}
@media only screen and (min-width: 1500px) {.slider{margin-top:-20px;} .slider .carousel-caption h2{font-size: 63px; line-height:75px; width: 100%;} .slider img{width:100% !important; height:auto !important;} .slider .carousel-caption .back{padding: 120px 40px; display: block;}}
.slider{position:relative; z-index: 9;}
.carousel-caption{
	top:10%;
	position:absolute;
	left:4%;
	right:0%;
	width:100%;
	text-align:left;
}
.slider .carousel-caption .back{
    background: url(images/shape-2.png) no-repeat top/cover;
    width: 100%;
    height: auto;
}
.slider .overlay:after{
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    background-image: -moz-linear-gradient(0deg, rgba(0, 0, 0, 0.80196) 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -webkit-linear-gradient(0deg, rgba(0, 0, 0, 0.80196) 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -ms-linear-gradient(0deg, rgba(0, 0, 0, 0.80196) 0%, rgba(0, 0, 0, 0) 100%);
    opacity: 0.8;
    z-index: 1;
}
.slider .carousel-caption h6{
    font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    text-transform: capitalize;
    color: #fff;
}
.slider .carousel-caption h6 img{width: 37px !important; height: 27px !important;}
.slider .carousel-caption h2{
	font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    text-transform: capitalize;
    color: #fff;
    margin-top: 25px;
    margin-bottom: 10px;
}
.slider .carousel-caption h2:after{
	position:absolute;
	height:2px;
	background:#ffff;
}
.slider .carousel-caption .btn{
	font-family: 'DM Sans', sans-serif;
    padding: 14px 35px;
    font-size: 16px;
    line-height: 20px;
    position: relative;
    border-radius: 12px;
    display: inline-block;
    color: #fff;
    background: #008bf9;
    text-align: center;
	margin-top:30px;
}
.slider .carousel-caption .btn:hover{
	background:#008bf9;
	color:#fff !important;
}
@keyframes borderAnimation { from { background-position: 0 0, -13.46px 0, 100% -13.46px, 0 100%; } to { background-position: 0 -13.46px, 0 0, 100% 0, -13.46px 100%; } } 
.slider .carousel.carousel-fade .carousel-item {
    display: block;
    opacity: 0;
    transition: opacity ease-out 1.7s;
    left: 0;
    top: 0;
    position: absolute;
}
.slider .carousel.carousel-fade .carousel-item.active {opacity: 1 !important;}
.slider .carousel.carousel-fade .carousel-item:first-child {
    top: auto;
    position: relative;
    transition: opacity ease-out 1.7s;
}
.slider .carousel.carousel-fade .carousel-item:second-child {
    top: auto;
    position: relative;
    transition: opacity ease-out 1.7s;
}
.slider .carousel-item .img-fluid {margin: 0 auto; width:100% !important;}
/***********============================================== slider =====================================================***********/

/***********============================================== section1 =====================================================***********/
@media only screen and (max-width: 600px) {.section1{padding:50px 20px 80px 20px;}}
@media only screen and (min-width: 600px) {.section1{padding:50px 20px 80px 20px;}}
@media only screen and (min-width: 768px) {.section1{padding:50px 20px 100px 20px;}} 
@media only screen and (min-width: 992px) {.section1{padding:50px 50px 100px 50px;}} 
@media only screen and (min-width: 1200px) {.section1{padding:50px 100px 100px 100px;}}
@media only screen and (min-width: 1500px) {.section1{padding:50px 160px 100px 160px;}}
.section1{position: relative;}
.section1 .mt30{margin-top:30px;}
.section1 .mt15{margin-top:15px;}
.section1 h6{
    font-family: 'DM Sans', sans-serif;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
    color: #008bf9;
    padding: 0 0 12px;
    margin: 0;
}
.section1 h2{
    font-family: 'DM Sans', sans-serif;
    position: relative;
    font-size: 45px;
    line-height: 52px;
    font-weight: 700;
    color: #0f3567;
}
.section1 .post-slide{margin: 0 15px;}
.section1 .block{
    border-radius: 20px;
    background: #fff;
    box-shadow: 0px 10px 60px rgba(15,53,103,.1);
    position: relative;
    z-index: 9 !important;
}
.section1 .img_bx{
    display: block;
    padding-bottom: 10px;
    border-radius: 20px;
    background: #edf7fe;
}
.section1 .block:hover .img_bx{background: #008bf9;}
.section1 .image img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center;
    object-position: center;
}
.section1 .image{
    position: relative;
    overflow: hidden;
    height: 254px;
    border-radius: 20px;
}
.section1 .block:hover .oh {opacity: 0.7; height: 200% !important; bottom: 10% !important;}
.section1 .oh {
    position: absolute;
    content: "";
    width: 25%;
    height: 100%;
    left: 0;
    background: #0f3567;
    transition: .3s ease-in-out;
    -ms-transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    bottom: -50%;
    transform: translateY(50%);
    opacity: 0;
}
.section1 .oh.ho_1 {
    transition: .3s ease-in-out;
    -ms-transition: .3s ease-in-out;
    -moz-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    left: 0%;
    transform: translateY(50%);
}
.section1 .oh.ho_2 {
    transition: .5s ease-in-out;
    -ms-transition: .5s ease-in-out;
    -moz-transition: .5s ease-in-out;
    -o-transition: .5s ease-in-out;
    left: 25%;
    transform: translateY(40%);
}
.section1 .oh.ho_3 {
    transition: .7s ease-in-out;
    -ms-transition: .7s ease-in-out;
    -moz-transition: .7s ease-in-out;
    -o-transition: .7s ease-in-out;
    left: 50%;
    transform: translateY(20%);
}
.section1 .oh.ho_4 {
    transition: .9s ease-in-out;
    -ms-transition: .9s ease-in-out;
    -moz-transition: .9s ease-in-out;
    -o-transition: .9s ease-in-out;
    transform: translateY(30%);
    left: 75%;
}
.section1 .block .content_box {
    padding: 30px 40px 40px;
    position: relative;
}
.section1 h4{
