@media only screen and (min-width: 1500px) {.banner{margin-top:-20px;} .banner .carousel-caption{top:25%;} .banner .carousel-caption h2{font-size: 40px; line-height:54px; width: 100%;} .banner img{width:100% !important; height:auto !important;} .banner .carousel-caption .back{padding: 120px 40px; display: block;}}
.banner{position:relative; z-index: 9;}
.carousel-caption{
	position:absolute;
	left:4%;
	right:0%;
	width:100%;
	text-align:left;
}
.banner .carousel-caption .back{
    width: 100%;
    height: auto;
}
.banner .overlay:after{
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    background-image: -moz-linear-gradient(0deg, rgba(0, 0, 0, 0.80196) 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -webkit-linear-gradient(0deg, rgba(0, 0, 0, 0.80196) 0%, rgba(0, 0, 0, 0) 100%);
    background-image: -ms-linear-gradient(0deg, rgba(0, 0, 0, 0.80196) 0%, rgba(0, 0, 0, 0) 100%);
    opacity: 0.8;
    z-index: 1;
}
.banner .carousel-caption h6{
    font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    text-transform: capitalize;
    color: #fff;
}
.banner .carousel-caption h6 img{width: 37px !important; height: 27px !important;}
.banner .carousel-caption h2{	
    font-family: 'DM Sans', sans-serif;
    font-weight: 700;
    text-transform: capitalize;
    color: #fff;
    text-shadow: 1px 1px #0f3567;
}
.banner .carousel-caption h2:after{
	position:absolute;
	height:2px;
	background:#ffff;
}
@keyframes borderAnimation { from { background-position: 0 0, -13.46px 0, 100% -13.46px, 0 100%; } to { background-position: 0 -13.46px, 0 0, 100% 0, -13.46px 100%; } } 
.banner .carousel.carousel-fade .carousel-item {
    display: block;
    opacity: 0;
    transition: opacity ease-out 1.7s;
    left: 0;
    top: 0;
    position: absolute;
}
.banner .carousel.carousel-fade .carousel-item.active {opacity: 1 !important;}
.banner .carousel.carousel-fade .carousel-item:first-child {
    top: auto;
    position: relative;
    transition: opacity ease-out 1.7s;
}
.banner .carousel.carousel-fade .carousel-item:second-child {
    top: auto;
    position: relative;
    transition: opacity ease-out 1.7s;
}
.banner .carousel-item .img-fluid {margin: 0 auto; width:100% !important;}
